.table {
    width: 100%;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    text-align: center;
    position: relative;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    max-width: 400px;
    margin: 0 auto;
    border-spacing: 1px;
    margin-bottom: 100px;
}

.table__cell {
    /*width: calc(10% - 4px);*/
    position: relative;
    border-radius: 1px;
    background-image: var(--cell-bg);
    -ms-flex-preferred-size: calc(10% - 4px);
        flex-basis: calc(10% - 4px);
    margin-right: 1px;
    margin-bottom: 1px;
}
.table__cell:last-child {
    margin-right: 20px;
}
.table__row {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    width: 100%;
    padding-right: 20px;
}
.first-cell {
    max-width: 20px;
    width: 20px;
    -ms-flex-positive: 0;
        flex-grow: 0;
}
.table__cell:before {
    content: '';
    display: block;
    padding-top: 100%;
}

.table__head {
    color: rgba(255,255,255,.5);
    position: relative;
    font-weight: normal;
    font-size: .75rem;
    background: none;
}

.digit {
    position: absolute;
    color: rgba(255,255,255,.5);
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    text-align: center;

    display: -ms-flexbox;

    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
}

.icon__lock {
    width: .75rem;
    height: .75rem;
}


